.btn-boom .boom-inner{
  position:relative;
  text-align: center;
  z-index:10;
  padding: 0 0 0;
}

.btn-boom{
  display:inline-block;
  width: 100%;
  max-width:100%;
  height:40px;
  line-height:48px;
  background:#f8f8f8;
  -webkit-perspective:300;
  box-shadow:0 -1px 2px rgb(255,255,255), inset 0 1px 2px rgba(0,0,0,.2), inset 0 .25rem 1rem rgba(0,0,0,.1);
  margin:0;
  position:relative;
}

.btn-boom .boom-hover{
	cursor: pointer;
  position:absolute;
  width:100%;
  height:100%;
  left:0;
  top:0;
  z-index:12;
  text-align: center;
  -webkit-transform-style: preserve-3d;
  background-image: linear-gradient(to top, rgba(0,0,0,0) 0%, rgba(0,0,0,.1) 100%);
}

.btn-boom-tw .boom-hover{
  background-color:#46d4fe;
  border:1px solid rgb(33,173,214);
}
.btn-boom-fb .boom-hover{
  background-color:#37589b;
  border:1px solid rgb(31,63,128);
}
.btn-boom-cv .boom-hover{
  background-color: rgba(255, 255, 255, 1);
  border: 1px solid rgba(23, 162, 184, .18%);
  border-bottom: 1px solid #fff;
}
.btn-boom-cv .out-text{
	color: #333;
}

.btn-boom:hover .boom-hover{
  -webkit-transform:rotateX(-120deg);
  -moz-transform:rotateX(-120deg);
  -ms-transform:rotateX(-120deg);
  transform:rotateX(-120deg);
  border-top-width:4px;
}

.btn-boom .boom-shadow,
.btn-boom .boom-hover,
.btn-boom .boom-overlay{
  transition:all .5s ease-in-out;
  -moz-transition:all .5s ease-in-out;
  -webkit-transition:all .5s ease-in-out;
  -webkit-transform-origin:center bottom;
  -ms-transform-origin:center bottom;
  -moz-transform-origin:center bottom;
  transform-origin:center bottom;
}

.btn-boom .boom-shadow{
  top:100%;
  position:absolute;
  width:100%;
  left:0;
  height:3.5rem;
  -webkit-transform-origin:center top;
  -ms-transform-origin:center top;
  -moz-transform-origin:center top;
  transform-origin:center top;
  -webkit-transform:rotateX(90deg);
  -moz-transform:rotateX(90deg);
  -ms-transform:rotateX(90deg);
  transform:rotateX(90deg);
  opacity:0;
  z-index:10;
  background-image:linear-gradient(to bottom, rgba(0,0,0,.15) 0, rgba(0,0,0,0) 50%);
}

.btn-boom:hover .boom-shadow{
  opacity:1;
  -ms-transform:rotateX(45deg) scale(.95);
  -moz-transform:rotateX(45deg) scale(.95);
  -webkit-transform:rotateX(45deg) scale(.95);
  transform:rotateX(45deg) scale(.95);
}

.btn-boom .boom-overlay{
  position:absolute;
  height:100%;
  width:100%;
  top:0;
  left:0;
  background:#000;
  z-index:10;
  opacity:0.5;
}

.btn-boom .boom-hover .out-text{
  font-size: 12px;
  font-weight: 700;
  position:relative;
  top:-4px;
}

.boom-inner a{
	text-decoration: none!important;
  font-size: 14px;
  position: relative;
  top: -4px;
  color: rgb(33 150 243);
  padding: 2px 0px 0;
  background-color: rgb(255 255 255);
  display: inline-block;
  width: 80%;
  line-height: 22px;
}

.btn-boom:hover .boom-overlay{
  opacity:0;
}