.oil{
    cursor: pointer;
    display: inline-block;
    height: 46px;
    width: 46px;
    margin: 0;
    outline: 0px solid #aaa;
    overflow: hidden;
    position: relative;
    vertical-align: middle;
    -webkit-perspective: 200px;
    -moz-perspective: 200px;
    -o-perspective: 200px;
    perspective: 200px;
    perspective-origin: center center;
	transition: transform 1s linear 0.8s;
}
.oil .b{
    display: block;
    position: absolute;
    height: 20px;
    width: 20px;
    margin: 0;
    -webkit-transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    -o-transform-style: preserve-3d;
    transform-style: preserve-3d;
    perspective: inherit;
	transition: transform 0.5s cubic-bezier(0.97, 0, 1, 1);
}
.oil .b:nth-of-type(1){
	color: #03A9F4;
    top: 0px;
    left: 16px;
	transform: rotateX(0deg);
}
.oil .b:nth-of-type(2){
	color: #FF9800;
    top: 16px;
    left: 0px;
	transform: rotateY(0deg);
}
.oil .b:nth-of-type(3){
    color: #4CAF50;
	top: 31px;
    left: 16px;
    transform: rotateX(0deg);
}
.oil .b:nth-of-type(4){
	color: #E91E63;
	top: 16px;
    left: 32px;
    transform: rotateY(0deg);
}
.oil .ox{
	border: 6px solid green;
	border-color: inherit;
    border-top-width: 0px;
    border-left-width: 0px;
    box-sizing: content-box;
    background-color: transparent;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 8px;
    width: 8px;
    margin: 0;
    transform: rotate(45deg);
    perspective: inherit;
    transform-style: preserve-3d;
}
.oil :nth-of-type(1) .ox{
	transform: rotate(45deg);
}
.oil :nth-of-type(2) .ox{
	transform: rotate(-45deg);
}
.oil :nth-of-type(3) .ox{
	transform: rotate(-135deg);
}
.oil :nth-of-type(4) .ox{
    transform: rotate(-225deg);
}
.oil .ox:before,
.oil .ox:after{
    content: "";
    display: block;
    background-color: #026d02;
	position: absolute;
}
.oil .ox:before{
    height: 14px;
    width: 8px;
    transform: translateZ(-4px) rotateY(90deg);
    top: 0px;
    left: 10px;
}
.oil .ox:after{
	height: 8px;
    width: 14px;
    transform: translateZ(-4px) rotateX(90deg);
    top: 10px;
    left: 0px;
}
.oil.hover{
	transform: rotate(90deg);
}
.oil.hover .b:nth-of-type(1){
	transform: rotateX(45deg);
}
.oil.hover .b:nth-of-type(2){
	transform: rotateY(-45deg);
}
.oil.hover .b:nth-of-type(3){
    transform: rotateX(-45deg);
}
.oil.hover .b:nth-of-type(4){
    transform: rotateY(45deg);
}

.oil .b:nth-of-type(1) .ox:before{
	background-color: #0090d1;
}
.oil .b:nth-of-type(1) .ox:after{
	background-color: #47c6ff;
}

.oil .b:nth-of-type(2) .ox:before{
	background-color: #f99a0e;
}
.oil .b:nth-of-type(2) .ox:after{
    background-color: #c67601;
}

.oil .b:nth-of-type(3) .ox:before{
	background-color: #5acc5f;
}
.oil .b:nth-of-type(3) .ox:after{
    background-color: #4CAF50;
}

.oil .b:nth-of-type(4) .ox:before{
	background-color: #dc0f55;
}
.oil .b:nth-of-type(4) .ox:after{
    background-color: #f93477;
}