.cv-logo_box{
  display: inline-block;
  width: 80%;
  padding-left: 8px;
  vertical-align: middle;
}
.cv-text_p{
  font-size: 15px;
  padding-top: 10px;
  text-indent: 10px;
}
.cv-ul{
  list-style: none;
  padding: 5px 15px 15px;
  margin: 0;
}
.cv-ul li{
	background-color: #fff;
  font-weight: 600;
  font-size: 14px;
  outline: 1px solid rgb(248, 249, 250);
  outline-offset: -2px;
  padding: 8px;
  box-shadow: inset 0px -12px 6px 0px #e4e4e4;
}
.cv-ul li:hover{
  z-index: 10;
}
.cv-ul__b{
  display: block;
  font-size: 12px;
  border-left: 1px solid rgb(223 223 223);
  padding-left: 8px;
}
.cv-ul__b2{
  display: block;
  font-size: 11px;
  padding-left: 0px;
  font-weight: 700;
  text-align: center;
}
.cv-ul__span{
  font-size: 14px;
}
.cv-h6{
	margin: 6px auto 0;
	text-transform: uppercase;
}
.cv-powerby{
  font-size: 12px;
  font-weight: 400;

}