.my-quality{
	outline: 1px solid #fff;
	color: #000;
	min-height: 120px;
	position: relative;
	width: 100%;
	box-shadow: inset 0px 0px 8px 0px rgba(23, 162, 184, 0.25);
  padding: 8px 0;
  overflow: hidden;
}
.my-quality_block,
.my-quality_subblock{
	cursor: pointer;
	border: 1px solid #ff7f50;
	position: absolute;
	top: 0;
	left: 30%;
	box-sizing: content-box;
}
.my-quality_block{
	transform: scale(0.8);
	transition: all 0.95s cubic-bezier(0.65, 0.05, 0.01, 0.97) 0s;
}
.my-quality_subblock{
	opacity: 0;
	transition: all 0.95s cubic-bezier(0.65, 0.05, 0.01, 0.97) 0s;
}

.my-quality_block{
	padding: 6px 12px;
}
.my-quality_subblock{
	background-color: rgb(240, 230, 140, .75);
	width: 62px;
  text-align: center;
	padding: 1px 6px;
	display: grid;
}
.my-quality_main{
	width: 158px;
  height: 14px;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	margin: auto;
}
.my-quality_title{
	color: inherit;
	display: block;
	font-family: sans-serif, monospace;
	font-size: 12px;
  line-height: 16px;
	text-align: center;
	text-transform: uppercase;
}
.my-quality_subtitle{
	font-family: system-ui, sans-serif;
  font-size: 70%;
}
.go .my-quality_subblock{
  opacity: 1;
}
.go .my-quality_block{
	transform: scale(1);
}
