.oil{
    cursor: pointer;
    display: inline-block;
    height: 46px;
    width: 46px;
    margin: 0;
    outline: 0px solid #aaa;
    overflow: hidden;
    position: relative;
    vertical-align: middle;
    -o-perspective: 200px;
    perspective: 200px;
    perspective-origin: center center;
	transition: transform 1s linear 0.8s;
}
.oil .b{
    display: block;
    position: absolute;
    height: 20px;
    width: 20px;
    margin: 0;
    transform-style: preserve-3d;
    perspective: inherit;
	transition: transform 0.5s cubic-bezier(0.97, 0, 1, 1);
}
.oil .b:nth-of-type(1){
	color: #03A9F4;
    top: 0px;
    left: 16px;
	transform: rotateX(0deg);
}
.oil .b:nth-of-type(2){
	color: #FF9800;
    top: 16px;
    left: 0px;
	transform: rotateY(0deg);
}
.oil .b:nth-of-type(3){
    color: #4CAF50;
	top: 31px;
    left: 16px;
    transform: rotateX(0deg);
}
.oil .b:nth-of-type(4){
	color: #E91E63;
	top: 16px;
    left: 32px;
    transform: rotateY(0deg);
}
.oil .ox{
	border: 6px solid green;
	border-color: inherit;
    border-top-width: 0px;
    border-left-width: 0px;
    box-sizing: content-box;
    background-color: transparent;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 8px;
    width: 8px;
    margin: 0;
    transform: rotate(45deg);
    perspective: inherit;
    transform-style: preserve-3d;
}
.oil :nth-of-type(1) .ox{
	transform: rotate(45deg);
}
.oil :nth-of-type(2) .ox{
	transform: rotate(-45deg);
}
.oil :nth-of-type(3) .ox{
	transform: rotate(-135deg);
}
.oil :nth-of-type(4) .ox{
    transform: rotate(-225deg);
}
.oil .ox:before,
.oil .ox:after{
    content: "";
    display: block;
    background-color: #026d02;
	position: absolute;
}
.oil .ox:before{
    height: 14px;
    width: 8px;
    transform: translateZ(-4px) rotateY(90deg);
    top: 0px;
    left: 10px;
}
.oil .ox:after{
	height: 8px;
    width: 14px;
    transform: translateZ(-4px) rotateX(90deg);
    top: 10px;
    left: 0px;
}
.oil.hover{
	transform: rotate(90deg);
}
.oil.hover .b:nth-of-type(1){
	transform: rotateX(45deg);
}
.oil.hover .b:nth-of-type(2){
	transform: rotateY(-45deg);
}
.oil.hover .b:nth-of-type(3){
    transform: rotateX(-45deg);
}
.oil.hover .b:nth-of-type(4){
    transform: rotateY(45deg);
}

.oil .b:nth-of-type(1) .ox:before{
	background-color: #0090d1;
}
.oil .b:nth-of-type(1) .ox:after{
	background-color: #47c6ff;
}

.oil .b:nth-of-type(2) .ox:before{
	background-color: #f99a0e;
}
.oil .b:nth-of-type(2) .ox:after{
    background-color: #c67601;
}

.oil .b:nth-of-type(3) .ox:before{
	background-color: #5acc5f;
}
.oil .b:nth-of-type(3) .ox:after{
    background-color: #4CAF50;
}

.oil .b:nth-of-type(4) .ox:before{
	background-color: #dc0f55;
}
.oil .b:nth-of-type(4) .ox:after{
    background-color: #f93477;
}
.btn-boom .boom-inner{
  position:relative;
  text-align: center;
  z-index:10;
  padding: 0 0 0;
}

.btn-boom{
  display:inline-block;
  width: 100%;
  max-width:100%;
  height:40px;
  line-height:48px;
  background:#f8f8f8;
  -webkit-perspective:300;
  box-shadow:0 -1px 2px rgb(255,255,255), inset 0 1px 2px rgba(0,0,0,.2), inset 0 .25rem 1rem rgba(0,0,0,.1);
  margin:0;
  position:relative;
}

.btn-boom .boom-hover{
	cursor: pointer;
  position:absolute;
  width:100%;
  height:100%;
  left:0;
  top:0;
  z-index:12;
  text-align: center;
  -webkit-transform-style: preserve-3d;
  background-image: linear-gradient(to top, rgba(0,0,0,0) 0%, rgba(0,0,0,.1) 100%);
}

.btn-boom-tw .boom-hover{
  background-color:#46d4fe;
  border:1px solid rgb(33,173,214);
}
.btn-boom-fb .boom-hover{
  background-color:#37589b;
  border:1px solid rgb(31,63,128);
}
.btn-boom-cv .boom-hover{
  background-color: rgba(255, 255, 255, 1);
  border: 1px solid rgba(23, 162, 184, .18%);
  border-bottom: 1px solid #fff;
}
.btn-boom-cv .out-text{
	color: #333;
}

.btn-boom:hover .boom-hover{
  transform:rotateX(-120deg);
  border-top-width:4px;
}

.btn-boom .boom-shadow,
.btn-boom .boom-hover,
.btn-boom .boom-overlay{
  transition:all .5s ease-in-out;
  -moz-transition:all .5s ease-in-out;
  -webkit-transition:all .5s ease-in-out;
  transform-origin:center bottom;
}

.btn-boom .boom-shadow{
  top:100%;
  position:absolute;
  width:100%;
  left:0;
  height:3.5rem;
  transform-origin:center top;
  transform:rotateX(90deg);
  opacity:0;
  z-index:10;
  background-image:linear-gradient(to bottom, rgba(0,0,0,.15) 0, rgba(0,0,0,0) 50%);
}

.btn-boom:hover .boom-shadow{
  opacity:1;
  transform:rotateX(45deg) scale(.95);
}

.btn-boom .boom-overlay{
  position:absolute;
  height:100%;
  width:100%;
  top:0;
  left:0;
  background:#000;
  z-index:10;
  opacity:0.5;
}

.btn-boom .boom-hover .out-text{
  font-size: 12px;
  font-weight: 700;
  position:relative;
  top:-4px;
}

.boom-inner a{
	text-decoration: none!important;
  font-size: 14px;
  position: relative;
  top: -4px;
  color: rgb(33 150 243);
  padding: 2px 0px 0;
  background-color: rgb(255 255 255);
  display: inline-block;
  width: 80%;
  line-height: 22px;
}

.btn-boom:hover .boom-overlay{
  opacity:0;
}
